import { useState, useEffect } from 'react';
import axios from 'axios';

const useOrderBook = (symbol, limit = 15) => {
  const [orderBook, setOrderBook] = useState({ bids: [], asks: [] });
  const [error, setError] = useState(null);

  useEffect(() => {
    const source = axios.CancelToken.source();

    const fetchOrderBook = async () => {
      try {
        setError(null);
        const response = await axios.get(`https://api.binance.com/api/v3/depth`, {
          params: {
            symbol: symbol,
            limit: limit,
          },
          cancelToken: source.token,
        });
        setOrderBook({
          bids: response.data.bids.sort((b, a) => parseFloat(a[0]) - parseFloat(b[0])),
          asks: response.data.asks.sort((a, b) => parseFloat(a[0]) - parseFloat(b[0])),
        });
      } catch (err) {
        if (!axios.isCancel(err)) {
          setError(err);
        }
      }
    };

    fetchOrderBook();

    const intervalId = setInterval(fetchOrderBook, 1000);

    return () => {
      clearInterval(intervalId);
      source.cancel('Operation canceled by the user.');
    };
  }, [symbol, limit]);

  return { orderBook, error };
};

export default useOrderBook;