import React, { useState } from 'react';
import Select from 'react-select';
import TradingView from "./components/TradingView";
import OrderBook from "./components/OrderBook";
import './app.css';

const symbolOptions = [
  { value: 'BTCUSDT', label: 'BTC/USDT' },
  { value: 'ETHBTC', label: 'ETH/BTC' },
  { value: 'LTCUSDT', label: 'LTC/USDT' },
  { value: 'XRPUSDT', label: 'XRP/USDT' },
];

const timeFrameOptions = ['1m', '5m', '15m', '1h', '12h'];

function App() {
  const [symbol, setSymbol] = useState({ value: 'BTCUSDT', label: 'BTC/USDT' });
  const [timeFrame, setTimeFrame] = useState('1m');

  const customSelectStyles = {
    control: (styles) => ({
      ...styles,
      backgroundColor: 'black',
      borderColor: 'gray',
      color: 'white',
      '&:hover': { borderColor: 'white' },
    }),
    option: (styles, { isFocused, isSelected }) => ({
      ...styles,
      backgroundColor: isSelected ? 'gray' : isFocused ? 'darkgray' : 'black',
      color: 'white',
      cursor: 'pointer',
      '&:active': { backgroundColor: 'gray' },
    }),
    menu: (styles) => ({
      ...styles,
      backgroundColor: 'black',
      color: 'white',
      zIndex: 9999
    }),
    singleValue: (styles) => ({
      ...styles,
      color: 'white',
    }),
    input: (styles) => ({
      ...styles,
      color: 'white',
    }),
    placeholder: (styles) => ({
      ...styles,
      color: 'gray',
    }),
  };


  return (
    <div className="app">
      <div className="toolbox">
        <Select
          defaultValue={symbol}
          options={symbolOptions}
          onChange={setSymbol}
          styles={customSelectStyles}
        />
        <div className="timeframe-selector">
          {timeFrameOptions.map((frame) => (
            <button
              key={frame}
              className={`timeframe-button ${timeFrame === frame ? 'active' : ''}`}
              onClick={() => setTimeFrame(frame)}
            >
              {frame}
            </button>
          ))}
        </div>
      </div>
      <div className="main-view">
        <div className="trading-view">
          <TradingView symbol={symbol.value} interval={timeFrame} />
        </div>
        <div className="order-book">
          <OrderBook symbol={symbol.value} />
        </div>
      </div>
    </div>
  );
}

export default App;