import React from 'react';
import useOrderBook from '../../hooks/useOrderBook';
import Histogram from "./Histogram";
import './index.css';

const OrderBook = ({ symbol }) => {
  const { orderBook, error } = useOrderBook(symbol);

  if (error) {
    return <div className="error">Error: {error.message}</div>;
  }

  if (!orderBook.bids.length || !orderBook.asks.length) {
    return <div className="loading">Loading...</div>;
  }

  const totalBids = orderBook.bids.reduce((acc, [_, amount]) => acc + parseFloat(amount), 0);
  const totalAsks = orderBook.asks.reduce((acc, [_, amount]) => acc + parseFloat(amount), 0);
  const maxTotal = Math.max(totalBids, totalAsks);

  return (
    <div className="orderbooks">
      <Histogram orders={orderBook.asks} color="#ef5350" maxTotal={maxTotal} isAsk={true} title="Asks" />
      <Histogram orders={orderBook.bids} color="#26a69a" maxTotal={maxTotal} isAsk={false} title="Bids" />
    </div>
  );
};

export default OrderBook;