import React from 'react';

const Histogram = ({ orders, color, maxTotal, isAsk, title }) => {
  const calculateCumulativeData = (orders, isAsk) => {
    let cumulativeQuantity = 0;
    const calculatedOrders = orders.map(([price, amount]) => {
      cumulativeQuantity += parseFloat(amount);
      const totalValue = parseFloat(price) * cumulativeQuantity;
      return { price, amount, cumulativeQuantity, totalValue };
    });
    return isAsk ? calculatedOrders.reverse() : calculatedOrders;
  };

  const data = calculateCumulativeData(orders, isAsk);

  return (
    <div className="histogram">
      <h2 style={{ color }}>{title}</h2>
      {data.map((order, index) => (
        <div key={index} className="histogram-row">
          <div className="histogram-bar" style={{ width: `${(order.cumulativeQuantity / maxTotal) * 100}%`, backgroundColor: color }}>
          </div>
          <div className="histogram-content">
            <span className="price">{parseFloat(order.price).toFixed(2)}</span>
            <span className="amount">{parseFloat(order.amount).toFixed(5)}</span>
            <span className="total">{order.totalValue.toFixed(2)}</span>
          </div>
        </div>
      ))}
    </div>
  );
};

export default Histogram;