import { useState, useEffect } from 'react';
import axios from 'axios';

const useTradingData = (symbol, interval, limit = 500) => {
  const [historicalData, setHistoricalData] = useState([]);
  const [error, setError] = useState(null);

  useEffect(() => {
    let isSubscribed = true;

    const fetchHistoricalData = async () => {
      try {
        setError(null);
        const response = await axios.get(`https://api.binance.com/api/v3/klines`, {
          params: {
            symbol: symbol,
            interval: interval,
            limit: limit,
          },
        });
        if (isSubscribed) {
          const processedData = response.data.map(kline => ({
            time: kline[0],
            open: parseFloat(kline[1]),
            high: parseFloat(kline[2]),
            low: parseFloat(kline[3]),
            close: parseFloat(kline[4]),
            volume: parseFloat(kline[5])
          }));
          setHistoricalData(processedData);
        }
      } catch (err) {
        if (isSubscribed) {
          setError(err);
        }
      }
    };

    fetchHistoricalData();

    const ws = new WebSocket(`wss://stream.binance.com:9443/ws/${symbol.toLowerCase()}@kline_${interval}`);

    ws.onmessage = (event) => {
      setError(null);
      const message = JSON.parse(event.data);
      const { k: kline } = message;

      setHistoricalData((prevData) => {
        const newData = [...prevData];
        const newCandlestick = {
          time: kline.t,
          open: parseFloat(kline.o),
          high: parseFloat(kline.h),
          low: parseFloat(kline.l),
          close: parseFloat(kline.c),
          volume: parseFloat(kline.v)
        };
        if (kline.x) {
          if (newCandlestick.time === newData[newData.length - 1].time) {
            newData[newData.length - 1] = newCandlestick;
            return newData;
          }
          newData.push(newCandlestick);
          if (newData.length > limit) {
            newData.shift();
          }
        } else {
          newData[newData.length - 1] = newCandlestick;
        }
        return newData;
      });
    };

    ws.onerror = (event) => {
      setError(event);
    };

    return () => {
      ws.close();
      isSubscribed = false;
    };
  }, [symbol, interval]);

  return { historicalData, error };
};

export default useTradingData;