import React, { useRef, useEffect } from 'react';
import { createChart, CrosshairMode, LineStyle, TickMarkType } from 'lightweight-charts';

const TradingViewChart = ({ data }) => {
  const chartContainerRef = useRef();
  const chart = useRef();

  useEffect(() => {
    chart.current = createChart(chartContainerRef.current, {
      width: chartContainerRef.current.clientWidth,
      height: chartContainerRef.current.clientHeight,
      layout: {
        background: { color: '#222' },
        textColor: '#DDD',
      },
      grid: {
        vertLines: {
          color: '#444'
        },
        horzLines: {
          color: '#444'
        },
      },
      crosshair: {
        mode: 1,
      },
      priceScale: {
        borderColor: '#71649C',
      },
      timeScale: {
        borderColor: '#71649C',
      },
    });

    const candleSeries = chart.current.addCandlestickSeries({
      upColor: 'rgba(38, 166, 154, 1)',
      downColor: 'rgba(255, 82, 82, 1)',
      borderDownColor: 'rgba(255, 82, 82, 1)',
      borderUpColor: 'rgba(38, 166, 154, 1)',
      wickDownColor: 'rgba(255, 82, 82, 1)',
      wickUpColor: 'rgba(38, 166, 154, 1)',
    });

    candleSeries.setData(data);

    chart.current.applyOptions({
      timeScale: {
        timeVisible: true,
        secondsVisible: false,
        tickMarkFormatter: (time, tickMarkType, locale) => {
          const date = new Date(time * 1000);
          if (tickMarkType === TickMarkType.Year) {
            return date.toLocaleDateString(locale, { year: 'numeric' });
          } else if (tickMarkType === TickMarkType.Month) {
            return date.toLocaleDateString(locale, { month: 'short', year: '2-digit' });
          } else if (tickMarkType === TickMarkType.Day) {
            return date.toLocaleDateString(locale, { day: 'numeric', month: 'short' });
          }
          return date.toLocaleTimeString(locale, { hour: '2-digit', minute: '2-digit' });
        },
      },
      crosshair: {
        mode: CrosshairMode.Normal,
        vertLine: {
          width: 8,
          color: '#C3BCDB44',
          style: LineStyle.Solid,
          labelBackgroundColor: '#9B7DFF',
        },
        horzLine: {
          color: '#9B7DFF',
          labelBackgroundColor: '#9B7DFF',
        },
      },
    });

    return () => {
      chart.current.remove();
    };
  }, [data]);

  useEffect(() => {
    const handleResize = () => {
      chart.current.applyOptions({
        width: chartContainerRef.current.clientWidth,
        height: chartContainerRef.current.clientHeight,
      });
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  return <div ref={chartContainerRef} className="chart-container" />;
};

export default TradingViewChart;