import React from 'react';
import TradingViewChart from './TradingViewChart';
import useTradingData from '../../hooks/useTradingData';
import './index.css';

const Chart = ({ symbol, interval }) => {
  const { historicalData, error } = useTradingData(symbol, interval);

  const tradingViewChartData = historicalData.map((d) => ({
    time: d.time / 1000,
    open: d.open,
    high: d.high,
    low: d.low,
    close: d.close,
  })).sort((a, b) => a.time - b.time);

  return (
    <TradingViewChart data={tradingViewChartData} />
  );
};

export default Chart;